  /**
   * 完全自定义节点外观方法
   */
  getShape() {
    const { model } = this.props;
    const { width, height, r, x, y } = model;
    const style = model.getNodeStyle();
    return h("g", {}, [
      h("circle", {
        ...style,
        r,
        cx: x,
        cy: y,
        width,
        height,
      }),
      this.getLabelShape(),
    ]);
  }
