import LogicFlow from "@logicflow/core";
const setContextPad = (lf: LogicFlow) => {
  const userConfig = {
    type: "h-bpmn:userTask",
    label: "用户任务",
    icon: "data:image/png;base64,",
  };
  const serviceConfig = {
    type: "h-bpmn:serviceTask",
    label: "系统任务",
    icon: "data:image/png;base64,",
    cls: "import_icon",
  };
  const exclusiveGatewayConfig = {
    type: "h-bpmn:exclusiveGateway",
    label: "条件判断",
    icon: "data:image/png;base64,",
  };
  const timerIntermediateEventConfig = {
    type: "h-bpmn:timerIntermediateEvent",
    label: "时间时间",
    icon: "data:image/png;base64,",
  };
  const endConfig = {
    type: "h-bpmn:endEvent",
    label: "结束",
    icon: "data:image/png;base64,",
  };
  const deleteConfig = {
    icon: "data:image/png;base64,",
    callback: (data: any) => {
      lf.deleteElement(data.id);
      lf.extension.contextPad.hideContextMenu();
    },
  };
  lf.extension.contextPad.setContextMenuItems([deleteConfig]);
  lf.extension.contextPad.setContextMenuByType("h-bpmn:startEvent", [
    userConfig,
    serviceConfig,
    exclusiveGatewayConfig,
    timerIntermediateEventConfig,
  ]);
  lf.extension.contextPad.setContextMenuByType(
    "h-bpmn:timerIntermediateEvent",
    [
      userConfig,
      serviceConfig,
      exclusiveGatewayConfig,
      timerIntermediateEventConfig,
    ]
  );
  lf.extension.contextPad.setContextMenuByType("h-bpmn:userTask", [
    userConfig,
    serviceConfig,
    exclusiveGatewayConfig,
    endConfig,
    timerIntermediateEventConfig,
  ]);
  lf.extension.contextPad.setContextMenuByType("h-bpmn:serviceTask", [
    userConfig,
    timerIntermediateEventConfig,
    serviceConfig,
    exclusiveGatewayConfig,
    endConfig,
  ]);
  lf.extension.contextPad.setContextMenuByType("h-bpmn:exclusiveGateway", [
    userConfig,
    serviceConfig,
    timerIntermediateEventConfig,
  ]);
};

export default setContextPad;
